module.exports = "const pagerInstance = (\n  <Pager>\n    <Pager.Item previous href=\"#\">\n      &larr; Previous\n    </Pager.Item>\n    <Pager.Item disabled next href=\"#\">\n      Next &rarr;\n    </Pager.Item>\n  </Pager>\n);\n\nrender(pagerInstance);\n"


