module.exports = "const pagerInstance = (\n  <Pager>\n    <Pager.Item previous href=\"#\">\n      &larr; Previous Page\n    </Pager.Item>\n    <Pager.Item next href=\"#\">\n      Next Page &rarr;\n    </Pager.Item>\n  </Pager>\n);\n\nrender(pagerInstance);\n"


