module.exports = "const modalInstance = (\n  <div className=\"static-modal\">\n    <Modal.Dialog>\n      <Modal.Header>\n        <Modal.Title>Modal title</Modal.Title>\n      </Modal.Header>\n\n      <Modal.Body>One fine body...</Modal.Body>\n\n      <Modal.Footer>\n        <Button>Close</Button>\n        <Button bsStyle=\"primary\">Save changes</Button>\n      </Modal.Footer>\n    </Modal.Dialog>\n  </div>\n);\n\nrender(modalInstance);\n"


