module.exports = "const navbarInstance = (\n  <Navbar>\n    <Navbar.Header>\n      <Navbar.Brand>\n        <a href=\"#home\">Brand</a>\n      </Navbar.Brand>\n      <Navbar.Toggle />\n    </Navbar.Header>\n    <Navbar.Collapse>\n      <Navbar.Text>\n        Signed in as: <Navbar.Link href=\"#\">Mark Otto</Navbar.Link>\n      </Navbar.Text>\n      <Navbar.Text pullRight>Have a great day!</Navbar.Text>\n    </Navbar.Collapse>\n  </Navbar>\n);\n\nrender(navbarInstance);\n"


