module.exports = "const gridInstance = (\n  <Grid>\n    <Row className=\"show-grid\">\n      <Col xs={12} md={8}>\n        <code>&lt;{'Col xs={12} md={8}'} /&gt;</code>\n      </Col>\n      <Col xs={6} md={4}>\n        <code>&lt;{'Col xs={6} md={4}'} /&gt;</code>\n      </Col>\n    </Row>\n\n    <Row className=\"show-grid\">\n      <Col xs={6} md={4}>\n        <code>&lt;{'Col xs={6} md={4}'} /&gt;</code>\n      </Col>\n      <Col xs={6} md={4}>\n        <code>&lt;{'Col xs={6} md={4}'} /&gt;</code>\n      </Col>\n      <Col xsHidden md={4}>\n        <code>&lt;{'Col xsHidden md={4}'} /&gt;</code>\n      </Col>\n    </Row>\n\n    <Row className=\"show-grid\">\n      <Col xs={6} xsOffset={6}>\n        <code>&lt;{'Col xs={6} xsOffset={6}'} /&gt;</code>\n      </Col>\n    </Row>\n\n    <Row className=\"show-grid\">\n      <Col md={6} mdPush={6}>\n        <code>&lt;{'Col md={6} mdPush={6}'} /&gt;</code>\n      </Col>\n      <Col md={6} mdPull={6}>\n        <code>&lt;{'Col md={6} mdPull={6}'} /&gt;</code>\n      </Col>\n    </Row>\n  </Grid>\n);\n\nrender(gridInstance);\n"


