module.exports = "const panelsInstance = (\n  <div>\n    <Panel bsStyle=\"primary\">\n      <Panel.Heading>\n        <Panel.Title componentClass=\"h3\">Panel heading</Panel.Title>\n      </Panel.Heading>\n      <Panel.Body>Panel content</Panel.Body>\n    </Panel>\n\n    <Panel bsStyle=\"success\">\n      <Panel.Heading>\n        <Panel.Title componentClass=\"h3\">Panel heading</Panel.Title>\n      </Panel.Heading>\n      <Panel.Body>Panel content</Panel.Body>\n    </Panel>\n\n    <Panel bsStyle=\"info\">\n      <Panel.Heading>\n        <Panel.Title componentClass=\"h3\">Panel heading</Panel.Title>\n      </Panel.Heading>\n      <Panel.Body>Panel content</Panel.Body>\n    </Panel>\n\n    <Panel bsStyle=\"warning\">\n      <Panel.Heading>\n        <Panel.Title componentClass=\"h3\">Panel heading</Panel.Title>\n      </Panel.Heading>\n      <Panel.Body>Panel content</Panel.Body>\n    </Panel>\n\n    <Panel bsStyle=\"danger\">\n      <Panel.Heading>\n        <Panel.Title componentClass=\"h3\">Panel heading</Panel.Title>\n      </Panel.Heading>\n      <Panel.Body>Panel content</Panel.Body>\n    </Panel>\n  </div>\n);\n\nrender(panelsInstance);\n"


