module.exports = "const buttonsInstance = (\n  <div>\n    <ButtonToolbar>\n      <DropdownButton\n        bsSize=\"large\"\n        title=\"Large button\"\n        id=\"dropdown-size-large\"\n      >\n        <MenuItem eventKey=\"1\">Action</MenuItem>\n        <MenuItem eventKey=\"2\">Another action</MenuItem>\n        <MenuItem eventKey=\"3\">Something else here</MenuItem>\n        <MenuItem divider />\n        <MenuItem eventKey=\"4\">Separated link</MenuItem>\n      </DropdownButton>\n    </ButtonToolbar>\n\n    <ButtonToolbar>\n      <DropdownButton title=\"Default button\" id=\"dropdown-size-medium\">\n        <MenuItem eventKey=\"1\">Action</MenuItem>\n        <MenuItem eventKey=\"2\">Another action</MenuItem>\n        <MenuItem eventKey=\"3\">Something else here</MenuItem>\n        <MenuItem divider />\n        <MenuItem eventKey=\"4\">Separated link</MenuItem>\n      </DropdownButton>\n    </ButtonToolbar>\n\n    <ButtonToolbar>\n      <DropdownButton\n        bsSize=\"small\"\n        title=\"Small button\"\n        id=\"dropdown-size-small\"\n      >\n        <MenuItem eventKey=\"1\">Action</MenuItem>\n        <MenuItem eventKey=\"2\">Another action</MenuItem>\n        <MenuItem eventKey=\"3\">Something else here</MenuItem>\n        <MenuItem divider />\n        <MenuItem eventKey=\"4\">Separated link</MenuItem>\n      </DropdownButton>\n    </ButtonToolbar>\n\n    <ButtonToolbar>\n      <DropdownButton\n        bsSize=\"xsmall\"\n        title=\"Extra small button\"\n        id=\"dropdown-size-extra-small\"\n      >\n        <MenuItem eventKey=\"1\">Action</MenuItem>\n        <MenuItem eventKey=\"2\">Another action</MenuItem>\n        <MenuItem eventKey=\"3\">Something else here</MenuItem>\n        <MenuItem divider />\n        <MenuItem eventKey=\"4\">Separated link</MenuItem>\n      </DropdownButton>\n    </ButtonToolbar>\n  </div>\n);\n\nrender(buttonsInstance);\n"


